import React, { useEffect } from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import { connect } from "react-redux";
import HomePage from "./pages/home-page/HomePage";
import ShopPage from "./pages/shop-page/ShopPage";
import ShopBYCategory from "./pages/shop-by-category/ShopByCategory";
import SignIn from "./components/authentication/SignIn";
import SignUp from "./components/authentication/SignUp";
import { fetchCurrentUser } from "./redux/actions";
import Header from "./components/header/Header";
import Checkout from "./components/checkout/Checkout";

function App(props) {
  const fetchCurrentUserRedux = async () => {
    await props.fetchCurrentUser();
  };
  useEffect(() => {
    fetchCurrentUserRedux();
    // eslint-disable-next-line
  }, []);
  const { currentUser, cartLength } = props;
  return (
    <div className="App ">
      <Header />
      <Switch>
        <Route exact path="/" component={HomePage} />
        <Route exact path="/shop" component={ShopPage} />
        <Route exact path="/shop/:category" component={ShopBYCategory} />
        <Route exact path="/sign in">
          {currentUser ? <Redirect to="/" /> : <SignIn />}{" "}
        </Route>
        <Route exact path="/sign up">
          {currentUser ? <Redirect to="/" /> : <SignUp />}{" "}
        </Route>
        <Route exact path="/checkout">
          {cartLength === 0 ? <Redirect to="/shop" /> : <Checkout />}{" "}
        </Route>
      </Switch>
    </div>
  );
}
const mapStateToProps = (state) => {
  return { currentUser: state.currentUser, cartLength: state.cart.length };
};
export default connect(mapStateToProps, { fetchCurrentUser })(App);
