import React, { useState } from "react";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import Alert from '@material-ui/lab/Alert';
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import { withRouter, Link } from "react-router-dom";
import SocialButton from "./SocialButton";
import axios from "axios";
import {connect} from "react-redux";
import {logInUser} from "../../redux/actions";
function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {"Copyright Â© "}
      <Link to="/">Your Website</Link> {new Date().getFullYear()}
    </Typography>
  );
}

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: "#191A1C",
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
    backgroundColor: "#191A1C",
    color: "#fff",
  },
  submitHover: {
    margin: theme.spacing(3, 0, 2),
    backgroundColor: "#fff!important",
    color: "#191A1C!important",
    fontWeight: "bold",
  },
}));

function SignIn(props) {
  document.title="CROWN Clothing - Sign in"
  const classes = useStyles();
  const [isSubmitHovered, setIsSubmitHovered] = useState(false);
  const [user, setUser] = useState({
    email: "",
    password: "",
  });
  const [passwordIsValid, setPasswordIsValid] = useState(true);
  const [emailIsValid, setEmailIsValid] = useState(true);
  const [isValid, SetIsValid] = useState();
  const [signInError,setSignInError]=useState(null);
  function passwordValidation(value) {
    let valid = false;
    if (value.length > 3) {
      return (valid = true);
    }
    return valid;
  }
  function emailValidation(value) {
    let valid = false;
    for (let i = 0; i < value.length; i++) {
      if (value[i] === "@") return (valid = true);
    }
    return valid;
  }
  function handleChange(event) {
    const { value, name } = event.target;
    setUser((preValue) => {
      return { ...preValue, [name]: value };
    });
    if (name === "password") {
      setPasswordIsValid(passwordValidation(value));
    } else {
      setEmailIsValid(emailValidation(value));
    }
  }

  function handleSubmit(event) {
    if (
      user.password.length !== 0 &&
      user.email.length !== 0 &&
      passwordIsValid &&
      emailIsValid
    ) {
      SetIsValid(false);
      axios
        .post("/api/login", {
          password: user.password,
          email: user.email,
        })
        .then(function (response) {
          if(response.data.success)
          {
          props.logInUser(response.data.currentUser)
          props.history.push("/shop");
          }
          else
          setSignInError(response.data.message);
        })
        .catch(function (error) {
          console.log(error);
        });
        
    }
    else{
      SetIsValid(true);
    }
    
    event.preventDefault();
  }
  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Sign in
        </Typography>
        <form className={classes.form} noValidate onSubmit={handleSubmit}>
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="email"
            label="Email Address"
            name="email"
            autoComplete="email"
            autoFocus
            value={user.email}
            onChange={handleChange}
            error={!emailIsValid}
            helperText={!emailIsValid && "Invalid email!"}
          />
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            name="password"
            label="Password"
            type="password"
            id="password"
            autoComplete="current-password"
            value={user.password}
            onChange={handleChange}
          />
          <FormControlLabel
            control={<Checkbox value="remember" color="primary" />}
            label="Remember me"
          />
          <Button
            type="submit"
            fullWidth
            variant="contained"
            onMouseOver={() => {
              setIsSubmitHovered(true);
            }}
            onMouseOut={() => {
              setIsSubmitHovered(false);
            }}
            className={isSubmitHovered ? classes.submitHover : classes.submit}
          >
            Sign In
          </Button>
          {isValid&&<Alert  severity="error">Invalid entry</Alert>}
          {signInError&&<Alert  severity="error">{signInError}</Alert>}
          <Grid container>
            <Grid item xs>
              <Link to="/">Forgot password?</Link>
            </Grid>
            <Grid item>
              <Link to="/sign up">{"Don't have an account? Sign Up"}</Link>
            </Grid>
          </Grid>
          <p className="pSeparator">
            <span className="separator">or</span>
          </p>
          
        </form>
        <SocialButton text="Login"></SocialButton>
      </div>
      <Box mt={8}>
        <Copyright />
      </Box>
    </Container>
  );
}
const mapStateToProps = (state) => {
  return { currentUser: state.currentUser };
};
export default withRouter(connect(mapStateToProps,{logInUser})(SignIn));
