import React from "react";
import Grid from '@material-ui/core/Grid';
import Item from "./Item";
import {withRouter} from "react-router-dom";
import "./shop.css";
import { Typography, Button } from "@material-ui/core";
function Collection(props){
    const reducedItems=props.items.filter((element)=>element.id<5);
    return <div >
    <Button className="collectionTitle">
    <Typography variant="h5" gutterBottom onClick={()=>{props.history.push(props.match.url+"/"+props.routeName)}}>{props.title}</Typography>
    </Button>
    <Grid container spacing={1}>
    {reducedItems.map((item)=>{
                return (<Item item={item} key={item.id} imageUrl={item.imageUrl} name={item.name} price={item.price}/>)
               })}
    
    </Grid>
    </div>
    
    ;}
export default withRouter(Collection);
