import React, { useState } from "react";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import { withRouter, Link } from "react-router-dom";
import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import "./authentication.css";
import SocialButton from "./SocialButton";
import axios from "axios";
import Alert from "@material-ui/lab/Alert";
import {connect} from "react-redux";
import {logInUser} from "../../redux/actions";

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {"Copyright Â© "}
      <Link to="/">Your Website</Link> {new Date().getFullYear()}
      {"."}
    </Typography>
  );
}

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: "#191A1C",
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(3),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
    backgroundColor: "#191A1C",
    color: "#fff",
  },
  submitHover: {
    margin: theme.spacing(3, 0, 2),
    backgroundColor: "#fff!important",
    color: "#191A1C!important",
    fontWeight: "bold",
  },
}));

function SignUp(props) {
  document.title="CROWN Clothing - Sign up"
  const classes = useStyles();
  const [isSubmitHovered, setIsSubmitHovered] = useState(false);
  const [user, setUser] = useState({
    username: "",
    email: "",
    password: "",
    lName: "",
  });
  const [passwordIsValid, setPasswordIsValid] = useState(true);
  const [emailIsValid, setEmailIsValid] = useState(true);
  const [usernameIsValid, setUsernameIsValid] = useState(true);
  const [lNameIsValid, setLNameIsValid] = useState(true);
  const [isValid, setIsValid] = useState();
  const [signUpError,setSignUpError]=useState(null);
  function passwordValidation(value) {
    let valid = false;
    if (value.length > 5) {
      return (valid = true);
    }
    return valid;
  }
  function usernameValidation(value) {
    let valid = false;
    if (value.length >= 2) {
      return (valid = true);
    }
    return valid;
  }
  function lNameValidation(value) {
    let valid = false;
    if (value.length >= 2) {
      return (valid = true);
    }
    return valid;
  }
  function emailValidation(value) {
    let valid = false;
    for (let i = 0; i < value.length; i++) {
      if (value[i] === "@") return (valid = true);
    }
    return valid;
  }
  function handleChange(event) {
    const { value, name } = event.target;
    setUser((preValue) => {
      return { ...preValue, [name]: value };
    });
    switch (name) {
      case "password":
        setPasswordIsValid(passwordValidation(value));
        break;
      case "username":
        setUsernameIsValid(usernameValidation(value));
        break;
      case "lName":
        setLNameIsValid(lNameValidation(value));
        break;
      case "email":
        setEmailIsValid(emailValidation(value));
        break;
      default:setIsValid(false)
        break;
    }
  }
  function handleSubmit(event) {
    if (
      (user.password.length !== 0 &&
        user.email.length !== 0 &&
        user.lName.length !== 0&&
      user.username.length !== 0&&
      passwordIsValid && emailIsValid)
    ) {
      setIsValid(false);
      axios
        .post("/api/register", {
          username: user.username,
          lName: user.lName,
          password: user.password,
          email: user.email,
        })
        .then(function (response) {
          console.log();
          if(response.data.success)
          {props.logInUser(response.data.currentUser);
          props.history.push("/shop");}
          else
          {
            setSignUpError(response.data.error);
          }
          
        })
        .catch(function (error) {
          console.log(error);
        });
    } else {
      setIsValid(true);
    }
    event.preventDefault();
  }
  return (
    <Container className="signUp" component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Sign up
        </Typography>
        <form className={classes.form} noValidate onSubmit={handleSubmit}>
          <Grid container spacing={2}>
            <Grid item xs={12} sm={6}>
              <TextField
                autoComplete="fname"
                name="username"
                variant="outlined"
                required
                fullWidth
                id="firstName"
                label="First Name"
                autoFocus
                onChange={handleChange}
                value={user.username}
                error={!usernameIsValid}
                helperText={!usernameIsValid && "Invalid first name!"}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                variant="outlined"
                required
                fullWidth
                id="lastName"
                label="Last Name"
                name="lName"
                autoComplete="lname"
                onChange={handleChange}
                value={user.lName}
                error={!lNameIsValid}
                helperText={!lNameIsValid && "Invalid last name!"}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                required
                fullWidth
                id="email"
                label="Email Address"
                name="email"
                type="email"
                autoComplete="email"
                onChange={handleChange}
                value={user.email}
                error={!emailIsValid}
                helperText={!emailIsValid && "Invalid email!"}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                required
                fullWidth
                name="password"
                label="Password"
                type="password"
                id="password"
                autoComplete="current-password"
                onChange={handleChange}
                value={user.password}
                error={!passwordIsValid}
                helperText={
                  !passwordIsValid &&
                  "The password must contain at least 6 characters or/and numbers"
                }
              />
            </Grid>
            <Grid item xs={12}>
              <FormControlLabel
                control={<Checkbox value="allowExtraEmails" color="primary" />}
                label="I want to receive inspiration, marketing promotions and updates via email."
              />
            </Grid>
          </Grid>
          <Button
            type="submit"
            fullWidth
            variant="contained"
            onMouseOver={() => {
              setIsSubmitHovered(true);
            }}
            onMouseOut={() => {
              setIsSubmitHovered(false);
            }}
            className={isSubmitHovered ? classes.submitHover : classes.submit}
          >
            Sign Up
          </Button>
          {isValid && <Alert severity="error">Invalid entry</Alert>}
          {signUpError&&<Alert severity="error">{signUpError}</Alert>}
          <Grid container justify="flex-end">
            <Grid item>
              <Link to="sign in">Already have an account? Sign in</Link>
            </Grid>
          </Grid>
          <p className="pSeparator">
            <span className="separator">or</span>
          </p>
        </form>
        <SocialButton className={classes.form} text="Continue"></SocialButton>
      </div>
      <Box mt={5}>
        <Copyright />
      </Box>
    </Container>
  );
};
const mapStateToProps = (state) => {
  return { currentUser: state.currentUser };
};
export default withRouter(connect(mapStateToProps,{logInUser})(SignUp));
