import React from "react";
import {
  FacebookLoginButton,
  GoogleLoginButton,
} from "react-social-login-buttons";

function SocialButton(props) {
  return (
    <div>
      <a
      href="/auth/facebook"
        style={{ textDecoration: "none" }}
      >
        <FacebookLoginButton>
          <p style={{ textAlign: "center",width:"15rem"}}>{props.text} with Facebook</p>
        </FacebookLoginButton>
      </a>
      <a
        href="auth/google"
        style={{ textDecoration: "none" }}
      >
        <GoogleLoginButton>
          <p style={{ textAlign: "center",width:"15rem"}}>{props.text} with Google</p>
        </GoogleLoginButton>
      </a>
    </div>
  );
}
export default SocialButton;
