import React from "react";
// import Item from "../shop/Item";

import CheckoutItem from "./CheckoutItem";
import {connect} from "react-redux";
import "./checkout.css";
import Payment from "./Payment";

const Checkout = ({items,cartLength}) => {
  document.title="Your Crown clothing shopping cart"
  let priceTotal=0;
  items.forEach(element => {
    priceTotal+=element.price*element.quantity;
  });
  return (
    <div className="checkout">
      <table>
        <thead>
          <tr>
            <th>Product</th>
            <th>Description</th>
            <th>Quantity</th>
            <th>Price</th>
            <th>Remove</th>
          </tr>
        </thead>
        <tbody>
          {items.map((item)=>{
          return (<CheckoutItem key={item._id} item={item}/>)
          })}
        </tbody>
      </table>
      <h2 className="total-price">TOTAL:${priceTotal}</h2>
      <Payment cartLength={cartLength} amount={priceTotal*100}/>
    </div>
  );
};
const mapStateToProps=(state)=>{
  return  {items:state.cart, cartLength:state.cart.length};
  };
export default connect(mapStateToProps)(Checkout);
