import React from "react";
import StripeCheckout from "react-stripe-checkout";
import { connect } from "react-redux";
import { Button } from "@material-ui/core";
import {withRouter} from "react-router-dom";
import CreditCardIcon from '@material-ui/icons/CreditCard';
import {handleToken,setCart}from "../../redux/actions";
const Payment = (props) => {
  return (
    <div>
      <StripeCheckout
        email={props.user && props.user.email}
        amount={props.amount}
        token={(token) => {
          props.handleToken(token,props.amount);
        }}
        stripeKey={process.env.REACT_APP_STRIP_KEY}
        closed={()=>{
         props.history.push("/success");
        }}
      >
      <Button startIcon={<CreditCardIcon />} className="payment-btn"  variant="contained" color="primary">BUY ({props.cartLength})</Button>
      </StripeCheckout>
    </div>
  );
};
const mapStateToProps = (state) => {
  return { user: state.currentUser };
};
export default withRouter(connect(mapStateToProps,{handleToken,setCart})(Payment));
