import React from "react";
import KeyboardArrowLeftIcon from "@material-ui/icons/KeyboardArrowLeft";
import DeleteIcon from "@material-ui/icons/Delete";
import IconButton from "@material-ui/core/IconButton";
import KeyboardArrowRightIcon from "@material-ui/icons/KeyboardArrowRight";
import {connect} from "react-redux";
import {addToCart,removeAllQuantityFromCart,removeOneFromCart} from "../../redux/actions"

const CheckoutItem = (props) => {
  const item = props.item;
  return (
    <tr>
      <td>
        <img src={item.imageUrl} alt={item.name} style={{marginTop:7}} width="93" height="135" />
      </td>
      <td>{item.name}</td>
      <td className="quantity">
        <IconButton aria-label="remove" size="small" onClick={() => {
          props.removeOneFromCart(item._id);
        }}>
          <KeyboardArrowLeftIcon />
        </IconButton>
        {item.quantity}
        <IconButton
          aria-label="add"
          size="small"
          onClick={() => {
            props.addToCart(item);
          }}
        >
          <KeyboardArrowRightIcon />
        </IconButton>
      </td>
      <td>{`$${item.price}`}</td>
      <td>
        <IconButton onClick={() => {
          props.removeAllQuantityFromCart(item._id);
        }}>
          <DeleteIcon />
        </IconButton>
      </td>
    </tr>
  );
};

export default connect(null,{addToCart,removeAllQuantityFromCart,removeOneFromCart})(CheckoutItem);
