export const addToCart=(item)=>{
    return {type:"ADD_TO_CART",payload:item};
}
export const removeOneFromCart=(itemId)=>{
    return {type:"REMOVE_ONE_QUANTITY",payload:{itemId}};
}
export const removeAllQuantityFromCart=(itemId)=>{
    return {type:"REMOVE_ALL_QUANTITY",payload:{itemId}};
}

export const handleToken=(token,amount)=>{
    return async(dispatch)=>{
    const response =await axios.post("/api/stripe",{token,amount});
    dispatch({type:"FETCH_CURRENT_USER",payload:response.data});
    }
}
export const setCart=(cart)=>{
    return {type:"SET_CART",payload:cart};
}
