import React,{useState} from "react";
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Grid from '@material-ui/core/Grid';
import CardMedia from '@material-ui/core/CardMedia';
import { Button,Zoom} from "@material-ui/core";
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { useTheme } from '@material-ui/core/styles';
import {connect}from "react-redux";
import {addToCart} from "../../redux/actions"

function Item(props){
    const theme = useTheme();
    const [showAddButton,setShowAddButton]=useState(false); 
    const isMobile = useMediaQuery(theme.breakpoints.down('xs'));
return (
    <Grid className="container" item xs={6} sm={6} lg={3} md={3}>
    
    <Card  className="item"onMouseOver={()=>{setShowAddButton(true)}} onMouseOut={()=>{setShowAddButton(false)}}>
    <CardMedia style={{height:!isMobile?"21rem":"15rem"}}
    image={props.imageUrl}
    component="img"
          alt="Contemplative Reptile"/>
    <CardContent className="item-content">
    <Zoom in={showAddButton}>
    <Button onMouseOver={()=>{setShowAddButton(true)}} onClick={()=>{props.addToCart(props.item)}} className="addButton">ADD TO CARD</Button>
    </Zoom>
    <p className="name">{props.name}  </p>
    <p className="price">{props.price}$</p>
   </CardContent>
    </Card>
   
    </Grid>
);
}

export default connect(null,{addToCart})(Item);