import React,{useState,useEffect} from "react";
import Grid from '@material-ui/core/Grid';
import axios from "axios";
import Item from "./Item";
import { Typography } from "@material-ui/core";
import _ from "lodash";
import "./shop.css"
//  import itemsList from "./shop.data";
import Loading from "../skeleton/Skeleton"
function Shop(props){
const [collection,setCollection]=useState([]);
const [isLoading,SetIsLoading]=useState(false);
const fetchCollection=async()=>{
    SetIsLoading(true)
const response= await axios.get(`/api/collection/${_.capitalize(props.name)}`);
SetIsLoading(false);
setCollection(response.data);

}
useEffect(()=>{
    fetchCollection();
    // eslint-disable-next-line
},[]);
// const {items,title}=itemsList.find((element)=>{
// return (_.capitalize(props.name)===element.title);
// });
const {items,title}=collection;
    return <div >
    <Typography variant="h3" gutterBottom> {title}</Typography>
    <Grid container spacing={1}>
    {isLoading&&<div><Loading /></div>}
     {  items&&items.map((item)=>{
      return (<Item item={item} key={item.id} imageUrl={item.imageUrl} name={item.name} price={item.price}/>)
     })}
    </Grid>
    </div>
    
    ;}
export default Shop;
