import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import Badge from "@material-ui/core/Badge";
import MenuIcon from "@material-ui/icons/Menu";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import { useTheme } from "@material-ui/core/styles";
import ShoppingBasketOutlinedIcon from "@material-ui/icons/ShoppingBasketOutlined";
import { withRouter } from "react-router-dom";
import Drawer from "@material-ui/core/Drawer";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import MailIcon from "@material-ui/icons/Mail";
import ShoppingCartIcon from "@material-ui/icons/ShoppingCart";
import LockOpenIcon from "@material-ui/icons/LockOpen";
import ExitToAppIcon from "@material-ui/icons/ExitToApp";
import VpnKeySharpIcon from "@material-ui/icons/VpnKeySharp";
import { connect } from "react-redux";

import "./header.css";
import crown from "./crown.png";
import { logoutUser } from "../../redux/actions";
import LogoutDialog from "../authentication/LogoutDialog";
// import PersonIcon from "@material-ui/icons/Person";
import Cart from "../Cart/Cart";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  title: {
    flexGrow: 1,
  },
  list: {
    width: 210,
  },
  fullList: {
    width: "auto",
  },
}));

function MenuAppBar(props) {
  const [open, setOpen] = useState(false);
  const [anchorEl, setAnchorEl] = useState(null);
  const { currentUser } = props;
  const classes = useStyles();
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down("xs"), { noSsr: true });
  const [state, setState] = useState({ right: false });

  const toggleDrawer = (anchor, open) => (event) => {
    if (
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };

  const mobileMenu = (
    <div>
      <IconButton
        edge="start"
        color="inherit"
        aria-label="menu"
        onClick={toggleDrawer("right", true)}
      >
        <MenuIcon />
      </IconButton>
      <Drawer
        anchor={"right"}
        open={state["right"]}
        onClose={toggleDrawer("right", false)}
      >
        <div
          className={classes.list}
          role="presentation"
          onClick={toggleDrawer("right", true)}
          onKeyDown={toggleDrawer("right", false)}
        >
          <List>
            <ListItem
              button
              onClick={() => {
                props.history.push("/shop");
              }}
            >
              <ListItemIcon>
                <ShoppingCartIcon></ShoppingCartIcon>
              </ListItemIcon>
              <ListItemText>SHOP</ListItemText>
            </ListItem>
            <ListItem button>
              <ListItemIcon>
                <MailIcon />
              </ListItemIcon>
              <ListItemText
                onClick={() => {
                  props.history.push("contact");
                }}
              >
                CONTACT
              </ListItemText>
            </ListItem>
            {!currentUser &&(
              <ListItem
                button
                onClick={() => {
                  props.history.push("/sign up");
                }}
              >
                <ListItemIcon>
                  <LockOpenIcon></LockOpenIcon>
                </ListItemIcon>
                <ListItemText>SIGN UP</ListItemText>
              </ListItem>
            ) }
            {!currentUser ? (
              <ListItem
                button
                onClick={() => {
                  props.history.push("/sign in");
                }}
              >
                <ListItemIcon>
                  <VpnKeySharpIcon></VpnKeySharpIcon>
                </ListItemIcon>
                <ListItemText>LOGIN</ListItemText>
              </ListItem>
            ) : (
              <ListItem
                button
                onClick={() => {
                  setOpen(true);
                }}
              >
                <ListItemIcon>
                  <ExitToAppIcon></ExitToAppIcon>
                </ListItemIcon>
                <ListItemText>LOGOUT</ListItemText>
              </ListItem>
            )}
          </List>
        </div>
      </Drawer>
    </div>
  );
  return (
    <div className={classes.root + " header"}>
      <Toolbar>
        <Typography variant="h6" className={classes.title}>
          <img
            onClick={() => {
              props.history.push("/");
            }}
            alt=""
            style={{ width: "65px", height: "40px" }}
            src={crown}
          />
        </Typography>
        {isMobile && (
          <IconButton
            aria-label="account of current user"
            aria-controls="menu-appbar"
            aria-haspopup="true"
            color="inherit"
            onClick={(event) => {
              if(props.cartLength!==0)
                  setAnchorEl(anchorEl ? null : event.currentTarget);
            }}
          >
            <Badge color="secondary" badgeContent={props.cartLength}>
              <ShoppingBasketOutlinedIcon />
            </Badge>
          </IconButton>
        )}
        <div>
          {isMobile ? (
            mobileMenu
          ) : (
            <div>
              <IconButton
                aria-label="account of current user"
                aria-controls="menu-appbar"
                color="inherit"
                className="navbarElement"
                onClick={() => {
                  props.history.push("/shop");
                }}
              >
                <Typography>SHOP</Typography>
              </IconButton>
              <IconButton
                aria-label="account of current user"
                aria-controls="menu-appbar"
                color="inherit"
                className="navbarElement"
                onClick={() => {
                  props.history.push("/contact");
                }}
              >
                <Typography>CONTACT</Typography>
              </IconButton>
              {!currentUser&&(
                <IconButton
                  aria-label="account of current user"
                  aria-controls="menu-appbar"
                  color="inherit"
                  className="navbarElement"
                  onClick={() => {
                    props.history.push("/sign up");
                  }}
                >
                  <Typography>SIGN UP</Typography>
                </IconButton>
              )}
              {currentUser ? (
                <IconButton
                  aria-label="account of current user"
                  aria-controls="menu-appbar"
                  color="inherit"
                  className="navbarElement"
                  onClick={() => {
                    setOpen(true);
                  }}
                >
                  <Typography>SIGN OUT</Typography>
                </IconButton>
              ) : (
                <IconButton
                  aria-label="account of current user"
                  aria-controls="menu-appbar"
                  color="inherit"
                  className="navbarElement"
                  onClick={() => {
                    props.history.push("/sign in");
                  }}
                >
                  <Typography>LOGIN</Typography>
                </IconButton>
              )}
              <IconButton
                aria-label="account of current user"
                aria-controls="menu-appbar"
                aria-haspopup="true"
                color="inherit"
                className="navbarElement"
                onClick={(event) => {
                  if(props.cartLength!==0)
                  setAnchorEl(anchorEl ? null : event.currentTarget);
                }}
              >
                <Badge color="secondary" badgeContent={props.cartLength}>
                  <ShoppingBasketOutlinedIcon style={{ fontSize: 25 }} />
                </Badge>
              </IconButton>
            </div>
          )}
        </div>
      </Toolbar>

      <LogoutDialog
        setOpen={setOpen}
        open={open}
        logoutUser={props.logoutUser}
      />
      <Cart anchorEl={anchorEl} setAnchorEl={setAnchorEl} />
    </div>
  );
}
const mapStateToProps = (state) => {
  return { currentUser: state.currentUser,cartLength:state.cart.length };
};

export default withRouter(connect(mapStateToProps, { logoutUser })(MenuAppBar));
