import React from "react";

const CartItem = (props) => {
  const item = props.item;
  return (
    <div className="cart-element">
      <img className="image" src={item.imageUrl} alt={item.name} width="54" height="75"></img>
      <div className="description">
        <h5>{item.name}</h5>
        <h6>{item.quantity===1?null:<span>{item.quantity}Ã</span>}{item.price}$</h6>
      </div>
    </div>
  );
};
export default CartItem;
